import React, { useState, useEffect } from 'react';
import './App.css';

var l = console.log;
var _ = s=>document.querySelectorAll(s);

function User({d}){
  l(d);
  let [first,last,age,gender,email] = [d.name.first,d.name.last,d.dob.age,d.gender,d.email];
  return(
    <div className="user" key={first+last+age} attr={(first+last).toLowerCase()+' '+age+' '+gender}>
      <img src={d.picture.large} alt="pic"/>
      <h2>{first+' '+last}</h2>
      <div className="label">Age:</div><div>{age}</div><br/>
      <div className="label">Gender:</div><div>{gender}</div><br/>
      <div className="label">Email:</div><div>{email}</div>           
    </div>);
}

function App({people}) {
  
  //var cc = 0;  
  let [name,setName] = useState();
  let [age,setAge] = useState([]);
  let [gender,setGender] = useState();
  let [filtered,setFiltered] = useState([]);
  let [pageNums,setPageNums] = useState([]);
  let [page,setPage] = useState(0);

  useEffect(()=>{
    //l(cc++,bb++);
    people.then(p=>{
      let list = p.filter(e=>{
        let [lName,lAge,lGender] = [(e.name.first+e.name.last).toLowerCase(),e.dob.age,e.gender];

        if((name && !(lName).includes(name)) || 
          (age[1] && (lAge < age[0] || lAge > age[1])) ||
          (gender && lGender !== gender))return false;
        else return true;
      });

      let len = list.length;
      _('#counter')[0].innerText = len+' result'+(len === 1?'':'s');

      let nums=[<div key="prev" className="prev" style={{visibility:'hidden'}} onClick={()=>move(0)}>Prev</div>];
      let pages = Math.ceil(len/6);
      for(let i = 0;i < pages;i++)nums.push(<span key={i} className={i?'':'current'} onClick={()=>setPage(i)}>{i+1}</span>);

      nums.push(<div key="next" className="next" style={{display:pages>1?'inline-block':'none'}} onClick={()=>move(1)}>Next</div>);
      setPageNums(nums);

      list = list.slice(page*6,(page*6)+6).map((e,i)=><User d={e} key={e.email} />)
      
      setFiltered(list);
      
      if(list.length < 6)return;
      _('#pager .prev')[0].style.visibility = page?'visible':'hidden';
      (_('#pager .current')[0]??{}).className = '';

      let spans = _('#pager span');
      spans[page].className = 'current';
      
      _('#pager .next')[0].style.display = page === spans.length -1?'none':'inline-block';
    })
    
    function move(p) {
      let e = _('#pager .current')[0][(p?'next':'previous')+'ElementSibling'];
      if(e.localName === 'span')e.click();
    }

  },[people,page,name,age,gender])
  
  
  function selectAge({target:e}){
    if(e.className === 'selected')return;

    // eslint-disable-next-line no-unused-expressions
    _('.ages .selected')[0]?.classList.remove('selected');
    e.className = 'selected';
    e.parentElement.previousElementSibling.innerText = e.innerText === 'none'?'':e.innerText;
    e.parentElement.style.display = 'none';
    setAge(e.innerText ==='none'?[]:e.innerText.split('-'));
    setTimeout(()=>e.parentElement.removeAttribute('style'),100);
  }

  function selectGender(e,isMale){
    if(e.classList.contains('selected')){
      e.classList.remove('selected');
      setGender();
      return;
    }

    let pre = isMale?'next':'previous';

    e.classList.add('selected');
    e[pre+'ElementSibling'][pre+'ElementSibling'].classList.remove('selected');
    setGender(isMale?'male':'female');
  }

  // function toggle(e){
  //   if(e.hasAttribute('style')){
  //     e.removeAttribute('style');
  //     //recognition.abort();
  //   }else {
  //     e.style.height = "247px";
  //     //recognition.start();

  //   }

  //}

  return (
    <>    
      <header>
        <div></div>
        <h1>Find a person</h1>
      </header>
      <aside role="search">
        <h2 onClick={({target})=>target.nextElementSibling.classList.toggle('reveal')}>Refine</h2>
        <div className="filters">
          <div className="label">Name</div>
          <div id="name" className="input" contentEditable="true" onInput={({target})=>setName(target.innerText.toLowerCase())}></div>
          <div className="label">Age</div>
          <div id="age" className="input age" onClick={({target})=>target.nextElementSibling.style.display = 'block'}></div>
          <div className="ages">
            <div age="none" onClick={selectAge}>none</div>
            <div age="0" data-po="2" onClick={selectAge}>0  - 10</div>
            <div age="10" onClick={selectAge}>10 - 20</div>
            <div age="20" onClick={selectAge}>20 - 30</div>
            <div age="30" onClick={selectAge}>30 - 40</div>
            <div age="40" onClick={selectAge}>40 - 50</div>
          </div>
          <div className="label">Gender</div >
          <div className="radiorow" gender="male" onClick={({currentTarget})=>selectGender(currentTarget,1)}><div className="radio">●</div><div>Male</div></div><br/>
          <div className="radiorow" gender="female" onClick={({currentTarget})=>selectGender(currentTarget)}><div className="radio">●</div><div>Female</div></div>
        </div>
      </aside>  
      <article>
        <h3 id="counter">0 results</h3>
        <div id="users">{filtered}</div>
        <div id="pager">{pageNums}</div>
      </article>
      <footer>
        <div>
          <a href="https://www.wmca.org.uk/careers">Careers</a>
          <a href="https://www.wmca.org.uk/documents">Documents</a>
          <a href="https://www.wmca.org.uk/policies">Policies</a>
          <a href="https://www.governance.wmca.org.uk/">Committee meetings</a>
          <a href="https://www.wmca.org.uk/contact-us">Contact us</a>
          <a href="https://www.wmca.org.uk/procurement">Procurement</a>
          <a href="https://www.wmca.org.uk/media-assets">Media Assets</a>
          <a href="https://www.wmca.org.uk/freedom-of-information">Freedom of Information</a>
        </div>
      </footer>
    </>
  );
}


export default App;
