import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';

//var l = console.log;
var _ = s=>document.querySelectorAll(s);

let people = fetch('people.json')
.then(d=>d.json())
.then(d=>d.results)
.catch(e=>[])

setTimeout(()=>{
  _('header')[0].style.backgroundImage = "url('https://chr1se.github.io/wmca-react-sample/library.jpg')";
  let s = document.createElement('style');
  s.innerText = '@font-face {font-family: "Texta-Regular"; src: url("https://chr1se.github.io/wmca-react-sample/TextaRegular.ttf");}';
  _('body')[0].append(s);
},500);

ReactDOM.render(
  <React.StrictMode>
    <App  people={people}/>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
